* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

:root {
  --white: #FFF;

  --gray-50: #F7F8FA;
  --gray-100: #E6E8EB;
  --gray-200: #AFB2B1;
  --gray-500: #808080;
  --gray-800: #494D4B;

  --sec-500: #D9CAC5;
  
  --pmr-300: #6C131E;
  --pmr-400: #6C131E; 
  --pmr-500: #6C131E;
  --pmr-800: #6C131E;
}

@media (max-width: 1080px) {
  html {
    font-size: 93.75%; // 15px
  }
}

@media (max-width: 720px) {
  html {
    font-size: 87.5%; // 14px
  }
}

body {
  background: var(--gray-50);
}

body, input, textarea, button {
  font: 500 1rem Inter, sans-serif;
  color: var(--gray-500);
}

h1, h2, h3, h4, h5, h6 {
  font-weight: 600;
  font-family: Lexend, sans-serif;
  color: var(--gray-800);
}

h1 {
  font-size: 2rem;
}

h2 {
  font-size: 1.5rem;
}

button {
  cursor: pointer;
}

.p-divider .p-divider-content{
  background-color: var(--gray-50);
}

.my-page-header{
  font-weight: 900;
}

.my-from{
  padding: 2rem;
}

.my-list{
  padding: 2rem;
}

.my-add-topic{
  margin-left: 2rem;
}

.my-icon {
  padding-top: 1rem;
}